// AboutUs.scss

.about-us-container {
    max-width: 800px;
    margin-top: 100px;
    margin-left: 30%;
    padding: 20px;
  
    h1 {
      font-size: 32px;
      margin-bottom: 20px;
    }
  
    p {
      font-size: 17.6px;
      line-height: 1.6;
      margin-bottom: 15px;
    }
  
    .mission-vision {
      display: flex;
      justify-content: space-between;
  
      .mission,
      .vision {
        flex: 1;
  
        h2 {
          font-size: 24px;
          margin-bottom: 10px;
        }
      }
    }
  
    .founders {
      margin-top: 30px;
  
      h2 {
        font-size: 24px;
        margin-bottom: 15px;
      }
    }
  
    .contact-info {
      margin-top: 30px;
  
      h2 {
        font-size: 24px;
        margin-bottom: 15px;
      }

      .button{
        background-color: rgb(0, 0, 0);
      }

      .button:hover{
        background-color: rgba(0, 0, 0, 0.514);
      }
    }
  }
  