.loading-screen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #6dc9cf79;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;

    .loading-spinner {
        border: 7px solid #f3f3f3;
        border-top: 7px solid hsl(0, 100%, 50%);
        border-radius: 50%;
        width: 100px;
        height: 100px;
        animation: spin 1.5s linear infinite;

        @keyframes spin {
            0% {
                transform: rotate(0deg);
            }
            100% {
                transform: rotate(360deg);
            }
        }
    }
}
