@import "../Mixins/Mixins.scss";


.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #28282894;
    color: #fff;
    z-index: 1000;
}

.logo-container {
    margin-right: 20px;
}

.logo {
    width: 80px;
    height: 50px;
}

.company-name {
    margin-left: 150px;
    font-size: 35px;

    @include md{
        display: none;
    }

    @include sd{
        display: none;
    }
}

.icons-container {
    display: flex;
}

.login {
    cursor: pointer;
    font-size: 25px;
    margin-right: 30px;
}

.icon {
    cursor: pointer;
    margin-right: 35px;
}
