*{
  margin: 0;
  padding: 0;
}


.contact-page {
    font-family: 'Arial', sans-serif;
    max-width: 800px;
    margin-top: 100px;
    margin-left: 30%;
    padding: 20px;
  
    .contact-info {
      display: flex;
      justify-content: space-between;
      margin-bottom: 40px;
  
      .info-item {
        flex: 1;
        padding: 20px;
        border: 1px solid #ccc;
        border-radius: 8px;
        margin: 20px;
  
        h2 {
          color: #000000;
        }
  
        p {
          color: #555;
          margin-bottom: 10px;
        }
      }
    }
  
    .contact-form {
      form {
        display: flex;
        flex-direction: column;
  
        label {
          color: #333;
          margin-bottom: 5px;
        }
  
        input,
        textarea {
          max-width: 100%;
          margin-bottom: 15px;
          padding: 10px;
          border: 1px solid #ccc;
          border-radius: 4px;
        }
  
        button {
          padding: 10px 20px;
          background-color: #000000;
          color: #fff;
          border: none;
          border-radius: 4px;
          cursor: pointer;
        }

        button:hover{
          background-color: #424242;
        }
      }
    }
  }
  