* {
    margin: 0px;
    padding: 0px;
}

.card-main {
    display: grid;
    grid-template-columns: auto auto auto auto;
    margin: 40px auto;
    justify-content: space-between;
    padding: 0 10px;
    row-gap: 20px;

    .card {
        background: #f8f8f8;
        box-shadow: 3px 4px 7px -6px rgb(0 0 0 / 60%);
        border-radius: 25px;
        margin: 30px 25px 0px 30px;
        padding: 10px;
        margin-bottom: 15px;
        cursor: pointer;
        max-width: 250px;
        max-height: 300px;

        h4 {
            font-family: system-ui;
            font-weight: 600;
            font-size: 20px;
            line-height: 26px;
            color: black;
            padding-top: 15px;
            text-align: center;
            text-decoration: none;
        }

        img {
            display: block;
            // border-radius: 5px;
            position: relative;
            height: 200px;
            width: 200px;
        }

        img:hover {
            transform: scale(1.07);
        }
    }
}
