.productimgs {
    margin-top: 100px;

.text{
    color: black;
    font-family: Arial, Helvetica, sans-serif;
    text-decoration: underline;
    text-decoration-color: blue;
    margin-left: 50px;
}

.card1 {
    background: #f8f8f800;
    box-shadow: 3px 4px 7px -6px rgba(193, 193, 193, 0);
    border-radius: 10px;
    margin: 50px 25px 0px 30px;
    padding: 10px;
    margin-bottom: 15px;
    cursor: pointer;
}

.card1 img {
    display: block;
    border-radius: 10px;
    position: relative;
    height: 250px;
    width: 300px;
}

.card1 h4 {
    font-family: system-ui;
    font-weight: 600;
    font-size: 20px;
    line-height: 26px;
    color: black;
    padding-top: 15px;
    text-align: center;
}

.sameclass {
    margin: 0%;
    flex-flow: wrap;
    justify-content: space-between;
    position: relative;
    display: none;
}

.category div.active {
    display: flex;
}

img {
    transition: transform;
}

img:hover {
    transform: scale(1.07);
}

}
