@import "../Mixins/Mixins.scss";



* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

.main{
    position: relative;
  }
  
  
  .footer {
    position: absolute;
    margin-left: 0px;
    margin-top: 100px;
    width: 100%;
    background-color: black;
    flex-flow: wrap;


    @include md{

      width: 50%;
    }
  }
  .footer .col {
    width: 190px;
    height: auto;
    float: left;
    box-sizing: border-box;
    padding: 20px;
  }
  .footer .col h2 {
    padding: 0;
    font-size: 17px;
    line-height: 17px;
    padding: 10px 0;
    color: #FF0063;
    text-transform: uppercase;
  }
  .footer .col ul {
    list-style-type: none;
    padding: 0;
  }
  .footer .col ul li {
    color: white;
    font-size: 15px;
    padding: 5px;
  }
  .address ul li {
    display: inline-block;
    padding-right: 5px !important;
  }
  
  .footer .col ul li:hover {
    cursor: pointer;
    color: lightgray;
    font-weight: bold;
  }
  
  .columnsubscribe{
    padding-top: 10px;
    padding-left: 0px;
  }
  
  input{
    width: 150%;
  }
  
  input,
  button {
    font-size: 12px;
    padding: 16px;
    border-radius: 5px;
    margin-bottom: 5px;
  }
  
  button {
    cursor: pointer;
    background-color: #c7940a;
    color: #ffffff;
  }
  
  button:hover{
    background-color: rgba(255, 227, 72, 0.779);
  }